body{
    margin: 0;
    padding: 0;
    }
    .main-searchBar{
        max-width: 330px;
        border: 2px solid  rgb(114, 169, 30) ;
        border-radius: 30px;
        padding: 0px 5px 4px 15px;
        display: flex;
    }
    .main-searchBar input{
        background: transparent;
        border: none;
        outline: none;
        width: 300px;
    }
    .search-icon{
        font-size: 30px;
        padding-top: 4px;
    }
    .search-icon:hover{
        font-size: 30px;
        cursor: pointer;
        color: rgb(114, 169, 30);
    
    }
    /* --------------------------categoriesNav-------------------------------- */
    .ul li{
        list-style: none;
        position: relative;
        
        
    }
    .ul li a{
        text-decoration: none;
        font-weight: 500;
        color: black;
    }
    .ul li a:hover{
        color: rgb(114, 169, 30);
    }
    
    .dropdown{
        display: none;
    }
    .ul li:hover .dropdown{
        display: block;
        position: absolute;
        left: 0;
        top: 100%;
        z-index: 1;
    }
    .dropdown ul{
        display: block;
        margin: 10px;
        background-color: rgb(191, 193, 193);
        border-radius: 0px 10px 10px 10px;
    }
    .dropdown ul li{
        width: 120px;
        padding: 5px;
    }
    .dropdown ul li a:hover{
        color: rgb(114, 169, 30);
        
    }
    
    /* --------------------------smsearchbar-------------------------------- */
    
    .sm-searchBar{
        border: 2px solid  rgb(114, 169, 30) ;
        border-radius: 30px;
        padding: 3px 5px 4px 15px;
        display: flex;
        margin-top: 5px;
    }
    .sm-searchBar input{
        background: transparent;
        border: none;
        outline: none;
        max-width: 100%;
    }
    .sm-search-icon{
        font-size: 30px;
        padding-bottom: 2px;
    }
    
    
    
    .listProduct .item img {
        width: 90%;
        filter: drop-shadow(0 50px 20px #0009);
    }
    .listProduct{
        max-width: 200px;
    }
    
    .listProduct .item {
        background-color: #EEEEE6;
        /* padding: 20px; */
        border-radius: 20px;
    }
    
    /* .listProduct .item h2 {
        
    } */
    
    .listProduct .item .price {
        letter-spacing: 2px;
        font-size: large;
    
    }
    
    .listProduct .item button {
        background-color: #353432;
        color: #eee;
        border: none;
        padding: 5px 10px;
        margin-top: 10px;
        border-radius: 20px;
        cursor: pointer;
    }
    
    .listProduct .item button:hover {
        background-color: #51504c;
    }
